import { execa, type Options as ExecaOptions } from 'execa';
import { logger } from './logger';

export interface ExecOptions extends ExecaOptions {
  command: string;
  args?: string[];
  silent?: boolean;
}

export const runCommand = async ({ command, args = [], silent, ...options }: ExecOptions) => {
  logger.debug('exec', { command, args });
  return execa(command, args, {
    stdout: silent ? 'pipe' : 'inherit',
    stderr: silent ? 'pipe' : 'inherit',
    stdin: 'inherit',
    ...options
  });
};
